<template>
<div class="seeUpLoad">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
    <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @upload="upload" dialogVisible:false></my-table>
       <my-toast :config="toastConfig" @close="toastConfig.dialogVisible=false" >
    <el-form :model="form" ref="ruleForm" label-width="100px" style="margin-right:30px;"> 
        <el-row>
        <el-col :span='8'>
            <el-form-item label="订单号">
                {{form.insideOrder}}
            </el-form-item>
            </el-col>
            <el-col :span='8'>
            <el-form-item label="客户信息">
                {{form.empName}}
            </el-form-item>
            </el-col>
             <el-col :span='8'>
            <el-form-item label="产品信息">
                {{form.prodName}}
                </el-form-item>
            </el-col>
              </el-row>
            <el-row>
                    <el-col :span='8'>
            <el-form-item label="工艺名称">
                {{form.crafworkName}}
                </el-form-item>
            </el-col>
            <el-col :span='8'>
            <el-form-item label="图片提交人">
                {{form.createBy}}
            </el-form-item>
            </el-col>
            <el-col :span='8'>
            <el-form-item label="上传日期">
                {{form.createTime}}
            </el-form-item>
            </el-col>
 </el-row>   
 <el-row>
     <el-col>
         <el-form-item >
     <img style="height:100px;max-width:80px" :src="'http://192.168.0.222:9000/group1/M00/00/04/wKgA3ls667uAR48AAAOw3jNPUrI321.jpg'"/>
             <img style="height:100px;max-width:80px" :src="'http://192.168.0.222:9000/group1/M00/00/04/wKgA3ls667uAR48AAAOw3jNPUrI321.jpg'"/>

            </el-form-item> 
         </el-col>
     </el-row>
 
        </el-form>
            <!-- <upload style="padding:0 20px;" :form="form"></upload> -->
            
        </my-toast>
    </div>
</template>
<script>
import { mapState } from "vuex";
import upload from "../../components/page.vue";
export default {
  data() {
    return {
      productList: [],
      deleteArr: [],
      selectData: [],
      tableData: [],
      tableConfig: {
        isHigh: false,
        isSelection: false,
        align: "center",
        loadShow: false,
        isPage: true,
        pageNo: 1,
        pageSize: 20,
        total: 0,
        colConfig: [
          //表格字段配置
          {
            field: "insideOrder",
            label: "订单号",
            type: "text"
          },
          {
            field: "empName",
            label: "客户信息",
            type: "text"
          },
          {
            field: "prodName",
            label: "产品信息",
            type: "text"
          },
          {
            field: "crafworkName",
            label: "工艺名称",
            type: "text"
          },
          {
            field: "createBy",
            label: "图片提交人",
            type: "text"
          },
          {
            field: "createTime",
            label: "上传日期",
            type: "text"
          },
          {
            label: "操作",
            type: "button",
            btnData: [
              {
                name: "查看详情",
                method: "upload"
              }
            ]
          }
        ]
      },
      searchData: {},
      searchConfig: [
        [
          {
            label: "订单编号",
            field: "insideOrder",
            type: "select",
            placeholder: "请选择订单编号",
            childrens: [],
            prop: {
              label: "insideOrder",
              value: "insideOrder"
            }
          },
          {
            label: "客户信息",
            field: "empName",
            type: "input",
            placeholder: "请输入客户信息"
          },
          {
            label: "产品信息",
            field: "prodName",
            type: "input",
            placeholder: "请输入产品信息"
          },
          {
            label: "上传日期",
            field: "createDate",
            type: "daterange"
          }
        ]
      ],
      toastConfig: {
        //弹框配置
        title: "上传资料",
        dialogVisible: false,
        width: "1000px",
        btnData: [
          {
            isShow: true,
            name: "关闭弹窗",
            method: "close"
          }
        ]
      },
      form: {
        fileList: []
      }
    };
  },
  computed: {
    ...mapState(["ports"])
  },
  components: {
    upload
  },
  mounted() {
    this.getData(); //获得表格数据
    this.$http.post(
      //下拉框
      this.ports + "/processPictMes/optionInsideOrder",
      {},
      data => {
        console.log(data, "111111");
        this.searchConfig[0][0].childrens = data;
        this.productList = data;
      },
      this
    );
  },
  methods: {
    upload(row, index) {
      this.$http.post(
        this.ports + "/processPictMes/findAllUploadPic",
        {
          insideOrder: row.insideOrder,
          prodNo: row.prodNo,
          crafworkId: row.crafworkId,
          customNo: row.customNo
        },
        data => {
          console.log(data);
          this.form = {
            insideOrder: row.insideOrder,
            prodName: row.prodName,
            prodNo: row.prodNo,
            crafworkName: row.crafworkName,
            crafworkId: row.crafworkId,
            empName: row.empName,
            customNo: row.customNo,
            createBy: row.createBy,
            createTime: row.createTime,
            empNo: row.empNo,
            pictures: Object.assign([], data),
            fileList: Object.assign([], data)
          };
          this.toastConfig.dialogVisible = true;
        },
        this
      );
    },
    close() {
      this.$http.post(
        this.ports + "/crafworkApprove/approveStatus",
        {
          id: this.form.id,
          cusIsOk: 3
        },
        data => {
          this.getData();
          this.toastConfig.dialogVisible = false;
        },
        this
      );
    },
    getData() {
      let param = JSON.parse(JSON.stringify(this.searchData));
      if (this.searchData.createDate) {
        param["startDate"] = param.createDate[0];
        param["endDate"] = param.createDate[1];
        delete param.createDate;
      }
      for (let i in param) {
        if (!param[i]) {
          delete param[i];
        }
      }
      this.$http.post(
        this.ports + "/processPictMes/pageAfterUpload",
        {
          pageNo: this.tableConfig.pageNo,
          pageSize: this.tableConfig.pageSize,
          ...param
        },
        data => {
          this.tableData = data.items;
          this.tableConfig.total = data.numRows;
        },
        this
      );
    },
    pageChange() {
      this.getData();
    },
    search() {
      this.tableConfig.pageNo = 1;
      this.tableConfig.pageSize = 20;
      this.getData();
    }
  }
};
</script>
<style lang="less">
    .seeUpLoad{
       padding:20px;
       background:#fff;
        .imgList{
            padding:0 20px;
            >div{
                width: 100px;
                height: 100px;
                position: relative;
                overflow: hidden;
                float: left;
                margin: 0px 10px;
                >img{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 100%;
                    transform: translate(-50%,-50%)
                }
            }
        }
    }
</style>


